import React, { useState, useEffect } from "react";
import { Button, Upload, List, message } from "antd";
import { Icon } from "@iconify/react";
import { InboxOutlined, DeleteOutlined } from "@ant-design/icons";
import "../../Styles/Custom.css";
import axios from "axios";

const Sider = ({ handleQuestion, handleActiveStatus }) => {
  const API_BASE_URL1 = "http://4.193.34.131:8080";
  const API_BASE_URL2 = "http://4.193.34.131:8081";
  const [activeButton, setActiveButton] = useState(1);
  const [loading, setLoading] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [upload, setUpload] = useState(false);
  const [Excels, setExcels] = useState([]);
  const [pdfs, setPdfs] = useState([]);
  const [sync, setSync] = useState(false);
  const [questions, setQuestions] = useState([]);
  const suggestions = [
    "Detail financing of the project in points",
    "Provide me a bar graph for gross income value based on the end year in dollars.​",
    "Summarize the financial model based on project milestone, financing and returns",
    "Provide me the details of debt structure of the project(with tenors, interest and total debt)",
  ];

  useEffect(() => {
    if (activeButton === 1) {
      const fetchExcelNames = async () => {
        try {
          const response = await axios.get(
            `${API_BASE_URL1}/uploaded/excel_names`
          );
          setExcels(response?.data?.uploaded_excels);
        } catch (error) {
          console.error("Error fetching Excel names:", error);
        }
      };
      fetchExcelNames();
      setSync(false);
    } else {
      const fetchPdfNames = async () => {
        try {
          const response = await axios.get(`${API_BASE_URL2}/list-pdf`);
          setPdfs(response?.data?.filenames);
        } catch (error) {
          console.error("Error fetching Excel names:", error);
        }
      };
      fetchPdfNames();
      setSync(false);
    }
  }, [sync, activeButton]);

  const handleButtonClick = (buttonNumber) => {
    handleActiveStatus(buttonNumber);
    setActiveButton(buttonNumber);
  };

  const handleFileExchange = async (file) => {
    setLoading(true);
    try {
      const response = await axios.post(
        `${API_BASE_URL2}/upload?filename=${file}.pdf`
      );
      setQuestions(response.data.questions);
      setLoading(false);
    } catch (error) {
      console.error("File upload failed:", error);
      setLoading(false);
    }
  };
  const handleDelete = async () => {
    setIsLoading(true);
    try {
      const response = await axios.post(
        `http://4.193.34.131:5000/llama-restart`
      );
      if (response.status === 200) {
        setTimeout(() => {
          setIsLoading(false);
          message.success("Deleted Successfully");
          setSync(true);
        }, 15000); // 5000 milliseconds = 5 seconds
      }
    } catch (error) {
      console.error("Error deleting file:", error);
      setIsLoading(false);
    }
  };
  const handleFileChange = async (file) => {
    if (activeButton === 1) {
      if (file.file) {
        const formData = new FormData();
        formData.append("excel", file.file);
        setUpload(true);

        try {
          const response = await axios.post(
            `${API_BASE_URL1}/upload/excel`,
            formData,
            {
              headers: {
                "Content-Type": "multipart/form-data",
              },
            }
          );
          const base64StringFromAPI = response.data.pdf_base64;
          const uint8Array = Uint8Array.from(atob(base64StringFromAPI), (c) =>
            c.charCodeAt(0)
          );
          const pdfFile = new File([uint8Array], "filename.pdf", {
            type: "application/pdf",
          });
          const pdfFormData = new FormData();
          pdfFormData.append("pdf", pdfFile);
          const Response = await axios.post(
            `${API_BASE_URL1}/upload/pdf`,
            pdfFormData
          );
          setUpload(false);
          setSync(true);
        } catch (error) {
          console.error("File upload failed:", error);
          setUpload(false);
        }
      }
    } else {
      const isBelow45KB = file.file.size / 1024 < 50;
      if (!isBelow45KB) {
        message.error("File size exceeds 50 KB limit");
        return false;
      } else {
        const formData = new FormData();
        formData.append("pdf", file.file);
        setUpload(true);
        const pdfForm = new FormData();
        pdfForm.append("file", file.file);

        try {
          const response1 = await axios.post(
            `${API_BASE_URL2}/upload/pdf`,
            formData,
            {
              headers: {
                "Content-Type": "multipart/form-data",
              },
            }
          );
          const response2 = await axios.post(
            `${API_BASE_URL2}/get-pdf`,
            pdfForm,
            {
              headers: {
                "Content-Type": "multipart/form-data",
              },
            }
          );
          setUpload(false);
          setSync(true);
        } catch (error) {
          console.error("File upload failed:", error);
          setUpload(false);
        }
      }
    }
  };

  return (
    <div className="siderbg">
      <h5 className="my-4 text-center text-dark">Select Model</h5>
      <div className="d-flex justify-content-around">
        <Button
          type="primary"
          onClick={() => handleButtonClick(1)}
          disabled={activeButton === 1}
          style={{
            backgroundColor: activeButton === 1 ? "white" : "#f0f2f5",
            color: activeButton === 1 ? "black" : "#ccc",
          }}
        >
          <div className="d-flex flex-column  align-items-center">
            <div className="d-flex gap-2">
              <img src="/Assets/excel.svg" alt="pdf" />
              <h6 className="mt-2">EXCEL</h6>
            </div>
            <p>For excel files</p>
          </div>
        </Button>

        <Button
          type="primary"
          onClick={() => handleButtonClick(2)}
          disabled={activeButton === 2}
          style={{
            backgroundColor: activeButton === 2 ? "white" : "#f0f2f5",
            color: activeButton === 2 ? "black" : "#ccc",
          }}
        >
          <div className="d-flex flex-column  align-items-center">
            <div className="d-flex gap-2">
              <img src="/Assets/pdf.svg" alt="pdf" />
              <h6 className="mt-2">PDF</h6>
            </div>
            <p>For pdf files</p>
          </div>
        </Button>
      </div>
      <div className="uploader">
        <Upload.Dragger
          name="files"
          customRequest={handleFileChange}
          accept={activeButton === 1 ? ".xlsx, .xls" : ".pdf"}
          showUploadList={false}
        >
          <p className="ant-upload-drag-icon">
            {upload ? (
              <Icon
                icon="eos-icons:three-dots-loading"
                color="#1890ff"
                fontSize={42}
              />
            ) : (
              <InboxOutlined />
            )}
          </p>
          <p className="ant-upload-text">
            {upload
              ? "Processing  Diffen..."
              : "Click or drag file to this area to upload"}
            {activeButton === 2 && !upload && (
              <p className="ant-upload-hint">Upload File up to 50KB</p>
            )}
          </p>
        </Upload.Dragger>
      </div>
      {activeButton === 1 ? (
        <>
          <h6 className="text-center mt-4 text-dark">Uploaded Documents</h6>
          <div className="scrollable-list px-3">
            <List
              size="small"
              dataSource={Excels}
              renderItem={(Excel) => (
                <List.Item
                  style={{ borderWidth: 0, borderColor: "transparent" }}
                >
                  <div
                    style={{
                      width: "100%",
                      border: "1px solid #d9d9d9",
                      padding: "8px",
                      margin: "4px 0",
                      borderRadius: "8px",
                    }}
                  >
                    {Excel}
                  </div>
                </List.Item>
              )}
            />
          </div>
        </>
      ) : (
        <>
          <div className="d-flex justify-content-around">
            <h6 className="text-center mt-4 text-dark">Uploaded Documents</h6>
            {pdfs.length > 0 && (
              <Button
                className="defaultbutton mt-3 text-dark"
                icon={<DeleteOutlined />}
                onClick={handleDelete}
              >
                Delete All
              </Button>
            )}
          </div>

          <div className="scrollable-lists px-3">
            <List
              size="small"
              loading={isLoading}
              dataSource={pdfs}
              renderItem={(pdf) => (
                <List.Item
                  style={{ borderWidth: 0, borderColor: "transparent" }}
                  onClick={() => {
                    handleFileExchange(pdf);
                  }}
                >
                  <div
                    style={{
                      width: "100%",
                      border: "1px solid #d9d9d9",
                      padding: "8px",
                      margin: "4px 0",
                      borderRadius: "8px",
                    }}
                  >
                    {pdf}
                  </div>
                </List.Item>
              )}
            />
          </div>
        </>
      )}
      {activeButton === 1 && (
        <>
          <h6 className="text-center mt-3 text-dark">Financial Modelling</h6>
          <div className="scrollable-list px-3">
            <List
              size="small"
              dataSource={suggestions}
              loading={loading}
              renderItem={(suggestion) => (
                <List.Item
                  style={{ borderWidth: 0, borderColor: "transparent" }}
                  onClick={() => {
                    handleQuestion(suggestion);
                  }}
                >
                  <div
                    style={{
                      width: "100%",
                      border: "1px solid #d9d9d9",
                      padding: "8px",
                      margin: "4px 0",
                      borderRadius: "8px",
                    }}
                  >
                    {suggestion}
                  </div>
                </List.Item>
              )}
            />
          </div>
        </>
      )}
      {activeButton === 2 && (
        <>
          <h6 className="text-center mt-3 text-dark">Document Support</h6>
          <div className="scrollable-list px-3">
            <List
              size="small"
              dataSource={questions}
              loading={loading}
              renderItem={(question) => (
                <List.Item
                  style={{ borderWidth: 0, borderColor: "transparent" }}
                  onClick={() => {
                    handleQuestion(question);
                  }}
                >
                  <div
                    style={{
                      width: "100%",
                      border: "1px solid #d9d9d9",
                      padding: "8px",
                      margin: "4px 0",
                      borderRadius: "8px",
                    }}
                  >
                    {question}
                  </div>
                </List.Item>
              )}
            />
          </div>
        </>
      )}
    </div>
  );
};

export default Sider;
